export const Skills = () => {
    return (
        <div id='skills' className={s.skillsBlock}>
            <Fade>
                <div className={`${container.container} ${s.skillsContainer}`}>
                    <Title title='Skills' />
                    <Fade>
                        <div className={s.skills}>
                            {skills.map(skill =>
                                <Skill title={skill.title} desciption={skill.description} icon={skill.icon} />)}
                        </div>
                    </Fade>
                </div>
            </Fade>
        </div>
    )
}
